.post {
    max-width: 260px;
    min-height: 280px;
    text-decoration: none;
    
    .img-wrapper {
        background: #D8D8D8;
        border-radius: 5px;
        width: 100%;
        overflow: hidden;
        height: 190px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .content-wrapper {
        padding: 6px;
        margin-bottom: 30px;

        .post-name {
            font-size: 16px;
            color: #000000;
        }

        .post-time {
            font-size: 12px;
            color: #4A4A4A;
            margin: 5px 0;
        }

        .post-price {
            font-family: 'Gilroy bold', sans-serif;
            font-weight: bold;
            font-size: 16px;
            color: #000000;
        }
    }
}