.extra-footer {
    width: 100%;
    padding: 50px 0;
    background: #33383C;

    .container {
        display: flex;
        flex-wrap: wrap;

        .box {
            width: 25%;

            .type-linker {
                margin-bottom: 20px;

                h3 {
                    font-family: 'Gilroy bold', sans-serif;
                    font-weight: bold;
                    font-size: 14px;
                    color: #FFFFFF;
                    position: relative;

                    &:before {
                        content: '';
                        background: url(./images/keyboard-right-arrow-button.svg) no-repeat;
                        width: 6px;
                        height: 9px;
                        position: absolute;
                        left: -15px;
                        top: 0;
                        bottom: 0;
                        margin: auto 0;
                    }
                }

                ul {
                    list-style: none;

                    li {
                        a {
                            font-family: 'Gilroy normal', sans-serif;
                            text-decoration: none;
                            font-size: 14px;
                            line-height: 29px;
                            color: #878F99;
                        }
                    }
                }
            }
        }

        .bottom-footer {
            width: 100%;
            padding-top: 20px;
            border-top: 2px solid #878F99;
            ul {
                list-style: none;
                width: 100%;
                display: flex;
                justify-content: space-between;

                li {
                    a {
                        font-family: 'Gilroy normal', sans-serif;
                        text-decoration: none;
                        font-size: 14px;
                        line-height: 29px;
                        color: #878F99;
                    }
                }
            }
        }
    }
}